  .profile-container {
    max-width: 600px;
    margin: 0 auto;
    padding: 20px;
    background-color: #f9f9f9;
    border: 1px solid #ccc;
    border-radius: 5px;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.5);
  }
  
  .profile-image {
    max-width: 200px;
    height: auto;
    display: block;
    margin: 0 auto;
    border-radius: 50%;
  }
  
  .profile-info {
    margin-top: 20px;
    text-align: center;
  }
  
  .profile-info h1 {
    font-size: 32px;
    margin-bottom: 10px;
  }
  
  .profile-info p {
    font-size: 18px;
    margin-bottom: 5px;
  }
  
  /* Small screens */
  @media (max-width: 576px) {
    form {
      max-width: 100%;
    }
    img{
      width: 80%;
    }
  }
  
  /* Medium screens */
  @media (min-width: 576px) and (max-width: 768px) {
    form {
      max-width: 500px;
    }
  }
  
  /* Large screens */
  @media (min-width: 768px) {
    form {
      max-width: 800px;
    }
  }
  